<template>
  <div class="el-row">
    <el-row style="padding: 20px 0;margin-left: 50px">
      <el-button></el-button>
      <el-button type="primary"></el-button>
      <el-button type="success"></el-button>
      <el-button type="info"></el-button>
      <el-button type="warning"></el-button>
      <el-button type="danger"></el-button>
    </el-row>
    <code-editor :code="elCode"></code-editor>
  </div>

</template>

<script>

    import CodeEditor from "./codeEditor";
    export default {
        name: "butElRow",
      components: {CodeEditor},
      data(){
        return{
          "elCode":[
            '<template>\n' +
            '   <el-row>\n' +
            '     <el-button></el-button>\n' +
            '     <el-button type="primary"></el-button>\n' +
            '     <el-button type="success"></el-button>\n' +
            '     <el-button type="info"></el-button>\n' +
            '     <el-button type="warning"></el-button>\n' +
            '     <el-button type="danger"></el-button>\n' +
            '   </el-row>\n' +
            '</template>'
          ],
        }
      }
    }
</script>

<style scoped>
  .el-row{

  }
</style>



