<template>
  <div class="word">
    <h3></h3>
    <p>
        UI.<br></p>
    <a href="https://github.com/8Innocence/teamWork">
      <!--<img src="../../../static/images/dl3.png" alt="">-->
    </a>
    <p>1.0.0  793825</p>
  </div>
</template>

<script>
    export default {
        name: "IndexWord"
    }
</script>

<style scoped>
  .word{
    width:100%;
    height:200px;
    /* background:pink; */
    margin-bottom:50px;

  }
  .word h3{
    font-size:50px;
    color:white;
    text-align:center;
    font-weight:500;
    text-shadow:2px 4px 5px black;


  }
  .word p{
    color:white;
    font-size:18px;
    text-align:center;
    margin:10px 30px;
    font-family: ;
    text-shadow:2px 2px 5px black;

  }
  .word a{
    text-align: center;
    display: block;
    height:60px;
    width:140px;
    margin:0 auto;
    border:1px solid white;
    line-height:60px;
    padding:0 15px;
    color:white;
    font-size:18px;
    text-decoration: none;
    text-shadow:2px 2px 5px black;

  }
  .word a img{
    width:20px;
    height:20px;
    margin:20px 5px 0 10px;
  }
  .word a:hover{
    border-radius:18px;
  }
</style>



