<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick" style="padding-left: 50px">
      <el-tab-pane label="" name="first"></el-tab-pane>
      <el-tab-pane label="" name="second"></el-tab-pane>
      <el-tab-pane label="" name="third"></el-tab-pane>
      <el-tab-pane label="" name="fourth"></el-tab-pane>
    </el-tabs>
    <code-editor :code="selfCode"></code-editor>
  </div>
</template>

<script>
    import CodeEditor from "./codeEditor";
    export default {
        name: "biaoqian",
      components: {CodeEditor},
      data() {
        return {
          selfCode:'<template>\n' +
          '    <el-tabs v-model="activeName" @tab-click="handleClick">\n' +
          '      <el-tab-pane label="" name="first"></el-tab-pane>\n' +
          '      <el-tab-pane label="" name="second"></el-tab-pane>\n' +
          '      <el-tab-pane label="" name="third"></el-tab-pane>\n' +
          '      <el-tab-pane label="" name="fourth"></el-tab-pane>\n' +
          '    </el-tabs>\n' +
          '</template>',
          activeName: 'second'
        };
      },
      methods: {
        handleClick(tab, event) {
          console.log(tab, event);
        }
      }
    }
</script>

<style scoped>

</style>



