          "        <div></div>\n" +
          "        <div></div>\n" +
          "      </el-collapse-item>\n" +
          "      <el-collapse-item title=\" Efficiency\" name=\"3\">\n" +
          "        <div></div>\n" +
          "        <div></div>\n" +
          "        <div></div>\n" +
          "      </el-collapse-item>\n" +
          "      <el-collapse-item title=\" Controllability\" name=\"4\">\n" +
          "        <div></div>\n" +
          "        <div></div>\n" +
          "      </el-collapse-item>\n" +
          "    </el-collapse>\n" +
          "</template>",
          activeNames: ['1']
        };
      },
      methods: {
        handleChange(val) {
          console.log(val);
        }
      }
    }
</script>

<style lang="scss" scoped>

</style>



